import { Component, ChangeDetectorRef, Renderer } from '@angular/core';
import { AuthProvidersService } from './providers/auth-providers.service';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { MediaMatcher } from '@angular/cdk/layout';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'app';
  mobileQuery: MediaQueryList;
  private _mobileQueryListener: () => void;

  currentUser: any;
  fillerNav = Array(50).fill(0).map((_, i) => `Nav Item ${i + 1}`);
  sub: any;
  isLevelTwoPage: any;
  constructor(
    public authData: AuthProvidersService,
    private router: Router,
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    private renderer: Renderer,
    private route: ActivatedRoute
  ) {
    this.mobileQuery = media.matchMedia('(max-width: 767px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);



    authData.currentUser().subscribe((data) => {
      this.currentUser = data;
    });

    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.checkIfAuthPages();
        this.isLevelTwoPage = ((event.url.split('/').length - 1) == 2) ? true : false;
      }
    });

  }

  logout() {
    this.authData.logoutUser().then(() => {
      this.router.navigate(['/login']);
    });
  }

  goBack() {
    window.history.go(-1)
  }

  checkIfAuthPages() {
    return window.location.href.indexOf("login") > -1 || window.location.href.indexOf("register") > -1
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }
}



