import { CampaignsService } from './../../../../providers/campains/campaigns.service';
import { AddFixtureComponent } from './../add-fixture/add-fixture.component';
import { Component, OnInit, ChangeDetectorRef, ElementRef } from '@angular/core';
import { MatDialog } from '@angular/material';
import { AngularFirestore } from 'angularfire2/firestore';
import { AngularFireDatabase } from 'angularfire2/database';
import { CompetitionProvidersService } from '../../../../providers/competitions/competition-providers.service';
import { ClubsProvidersService } from '../../../../providers/clubs/clubs-providers.service';
import { MediaMatcher } from '@angular/cdk/layout';
import { TooltipPosition } from '@angular/material';
import { FixturesProvidersService } from '../../../../providers/fixtures/fixtures-providers.service';
import { UpdateFixtureComponent } from '../update-fixture/update-fixture.component';
import { FormControl, FormGroup, Validators, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-fixtures',
  templateUrl: './fixtures.component.html',
  styleUrls: ['./fixtures.component.scss']
})
export class FixturesComponent implements OnInit {
  filteredClubsData: any[];
  filteredFixturesData: any[];
  allFixtures: any[];
  campaignsData: any[];
  clubData: any;
  clubsData: any[];
  fixturesData: any;
  floatBtnShown: boolean = true;
  selectedTab: any;
  private _mobileQueryListener: () => void;
  loading: boolean;
  mobileQuery: MediaQueryList;
  competitionsData: any[];
  competitionID: any;
  
  fixturesFilters = new FormGroup({
    competitionFilterInput: new FormControl(),
    seasonFilter: new FormControl(),
    clubFilter: new FormControl()
  });

  constructor(
    public dialog: MatDialog,
    private afs: AngularFirestore,
    private af: AngularFireDatabase,
    public competitionServices: CompetitionProvidersService,
    public clubsService: ClubsProvidersService,
    private formBuilder: FormBuilder,
    public fixturesService: FixturesProvidersService,
    public campaignsService: CampaignsService,
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher
  ) {
    this.mobileQuery = media.matchMedia('(max-width: 767px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);

    this.loading = true;
    clubsService.getClubs().valueChanges().subscribe((data) => {
      this.clubsData = data;
      this.competitionID = data[0].id;
      this.fixturesFilters = this.formBuilder.group({
        competitionFilterInput: data[0].id,
        seasonFilterInput: null,
        clubFilterInput: null
      });

      this.loading = false;
    })
  }

  ngOnInit() {
    window.scrollTo(0, 0);
    this.getCompetitions();
    this.getAllFixtures();
    this.getCampaigns();
  }

  getAllFixtures() {
    this.loading = true;
    this.fixturesService.getAllFixtures().valueChanges().subscribe((data) => {
      this.allFixtures = data;
      this.fixturesData = data;
      this.loading = false;
    })
  }

  getCompetitions() {
    this.loading = true;
    this.competitionServices.getCompetitions().valueChanges().subscribe((data) => {
      this.competitionsData = data;
      this.resetFilterForm();
      this.onCompetitionChange(this.fixturesFilters.value.competitionFilterInput);
      this.filteredClubsData = this.clubsData.filter(item => item.competitions.indexOf(this.fixturesFilters.value.competitionFilterInput) > -1);
      this.loading = false;
    })
  }

  getCampaigns() {
    this.loading = true;
    this.campaignsService.getCampaigns().valueChanges().subscribe((data) => {
      this.campaignsData = data;
      this.loading = false;
    })
  }

  onCompetitionChange(id) {
    this.competitionID = id;
    if(id) {
      this.fixturesService.getFixturesByCompetitionID(id).subscribe((data) => {
        this.filteredFixturesData = data;
        this.fixturesData = this.filteredFixturesData;
        this.filteredClubsData = this.clubsData.filter(item => item.competitions.indexOf(id) > -1);
      })
    }
    else {
      this.fixturesService.getAllFixtures().valueChanges().subscribe((data) => {
        this.fixturesData = data;
        this.filteredClubsData = this.clubsData;
      });
    }
  }

  onClubChange(id:Event):void {
    if(id) {
      this.fixturesData = this.filteredFixturesData.filter(item => item.homeClub === id || item.awayClub === id);
    }
    else {
      this.fixturesData = this.filteredFixturesData;
    }
  }
  
  onSeasonChange(id:Event):void {
    console.log(id);
  }

  onSelect(e) {
    this.floatBtnShown = false;
    setTimeout(() => {
      this.floatBtnShown = true;
    }, 600);
  }

  public getClubDetails(id) {
    if(this.clubsData) {
      let club = this.clubsData.find(item => item.id === id);
      return club.name;
    }
  }

  resetFilterForm() {
    this.fixturesFilters = this.formBuilder.group({
      competitionFilterInput: this.competitionsData[4].id,
      seasonFilterInput: null,
      clubFilterInput: null
    });
  }

  clearFilters() {
    this.fixturesFilters = this.formBuilder.group({
      competitionFilterInput: '',
      seasonFilterInput: null,
      clubFilterInput: null
    });
    this.onCompetitionChange(this.fixturesFilters.value.competitionFilterInput);
  }
}



