import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { CompetitionProvidersService } from '../../../providers/competitions/competition-providers.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ClubsProvidersService } from '../../../providers/clubs/clubs-providers.service';
@Component({
  selector: 'app-manage-groups',
  templateUrl: './manage-groups.component.html',
  styleUrls: ['./manage-groups.component.scss']
})
export class ManageGroupsComponent implements OnInit {
  competitionTypes:any;
  loading: boolean;
  clubsData: any;
  form: FormGroup;
  selectedClubs = [];
  competitionData: any;
  constructor(
    public dialogRef: MatDialogRef<ManageGroupsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public competitionServices: CompetitionProvidersService,
    public clubsService: ClubsProvidersService,
    private formBuilder: FormBuilder) {
      console.log(data);

    competitionServices.getCompetitionTypes().valueChanges().subscribe((data) => {
      this.competitionTypes = data;
    });

    this.form = this.formBuilder.group({
      club: null,
    });

  }

  ngOnInit() {
    this.getClubs();
    this.getClubsInGroup();
    this.getGroupsArray();
  }

  getClubsInGroup() {
    let clubsInGroup = [];
    for (var i = 1; i <= this.data.numberOfClubsPerGroup; i++) {
      clubsInGroup.push(i);
    }
    return clubsInGroup;
  }

  getGroupsArray() {
    let groups = [];
    for (var i = 1; i <= this.data.numberOfGroups; i++) {
      groups.push(i);
    }
    return groups;
  }

  getCompetitionType(id) {
    this.competitionServices.getCompetitionByID(id).then( data => {
      return data;
    }) 
  }

  getClubs() {
    this.loading = true;
    this.clubsService.getClubs().valueChanges().subscribe((data) => {
      if(this.data.id && data) {
        this.clubsData = data.filter(item => item.competitions.indexOf(this.data.id) > -1);
        this.loading = false;
      }
    })
  }

  onClubSelect(club, group) {
    this.selectedClubs.push({club: club, group: group.group});
  }

  removeClub(club) {
    this.selectedClubs = this.selectedClubs.filter(item => item.club !== club);
  }

  checkIfClubSelected(id) {
    return this.selectedClubs.find(item => item.club == id);
  }

  onCancel(): void {
    this.dialogRef.close();
  }

  addGroups() {
    this.competitionData = this.data;
    this.competitionData.groupedClubs = this.selectedClubs.length > 0 ? this.selectedClubs : null;
    this.competitionServices.updateCompetition(this.competitionData);
  }
}



