import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CompetitionProvidersService } from '../../../providers/competitions/competition-providers.service';
import { ClubsProvidersService } from '../../../providers/clubs/clubs-providers.service';
import { FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-competition',
  templateUrl: './competition.component.html',
  styleUrls: ['./competition.component.scss']
})
export class CompetitionComponent implements OnInit {
  competitionId: any;
  loading: boolean;
  competitionData: any;
  competitionTypeData: any;
  clubsData: any[];

  constructor(
    private route: ActivatedRoute,
    private activeRoute: ActivatedRoute,
    public competitionServices: CompetitionProvidersService,
    public clubsService: ClubsProvidersService,
    private formBuilder: FormBuilder){

    this.activeRoute.params.subscribe(params => {
      this.competitionId = params.id;
    });

  }

  ngOnInit() {
    window.scrollTo(0, 0);
    this.getCompetition();
    this.getClubs();
  }

  getCompetition() {
    this.loading = true;
    this.competitionServices.getCompetitionByID(this.competitionId).then(data => {
      this.competitionData = data;
      this.getCompetitionType(this.competitionData.type);
      this.loading = false;
    });
  }

  getClubs() {
    this.loading = true;
    this.clubsService.getClubs().valueChanges().subscribe((data) => {
      if(this.competitionId && data) {
        this.clubsData = data.filter(item => item.competitions.indexOf(this.competitionId) > -1);
        this.loading = false;
      }
    })
  }

  getCompetitionType(id) {
    this.competitionServices.getCompetitionTypeByID(id).then( data => {
      this.competitionTypeData = data;
    })
  }
}



