import { Component, OnInit, Input, ChangeDetectorRef } from '@angular/core';
import { FixturesProvidersService } from '../../../providers/fixtures/fixtures-providers.service';
import { MediaMatcher } from '@angular/cdk/layout';
import { MatDialog } from '@angular/material';
import { AddFixtureComponent } from '../../../pages/matches/fixtures/add-fixture/add-fixture.component';
import { UpdateFixtureComponent } from '../../../pages/matches/fixtures/update-fixture/update-fixture.component';

@Component({
  selector: 'app-fixtures-listing',
  templateUrl: './fixtures-listing.component.html',
  styleUrls: ['./fixtures-listing.component.scss']
})
export class FixturesListingComponent implements OnInit {
  @Input() competitionID: any;
  @Input() clubsData: any[];
  @Input() competitionType: any;
  private _mobileQueryListener: () => void;
  mobileQuery: MediaQueryList;
  fixturesData: any;
  type: any = 'fixture';
  constructor(
    public fixturesService: FixturesProvidersService,
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    public dialog: MatDialog) {
      
    this.mobileQuery = media.matchMedia('(max-width: 767px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
    }

  ngOnInit() {
    this.getFixtures(this.competitionID);
    console.log('competitionType', this.competitionType);
  }

  getFixtures(id) {
    this.fixturesService.getFixturesByCompetitionID(id).subscribe((data) => {
      this.fixturesData = data;
    })
  }

  public getClubDetails(id) {
    if(this.clubsData) {
      let club = this.clubsData.find(item => item.id === id);
      return club.name;
    }
  }

    // ACTIONS
    openAddFixture(event, id) {
      this.dialog.open(AddFixtureComponent, {
        width: '700px',
        data: {competition: id}
      });
    }
    
    editFixture(item) {
      this.dialog.open(AddFixtureComponent, {
        width: '700px',
        data: item
      });
    }
  
    updateResults(item) {
      // add results
      this.dialog.open(UpdateFixtureComponent, {
        width: '700px',
        data: item
      });
    }
  
    deleteFixture(item) {
      this.fixturesService.deleteFixture(this.type, item);
    }

}



