import { ManageGroupsComponent } from './../manage-groups/manage-groups.component';
import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatTableDataSource, MatSort, MatDialogConfig } from '@angular/material';
import { CompetitionsModel } from '../../../models/competitions-model';
import { AngularFirestore } from 'angularfire2/firestore';
import { CompetitionProvidersService } from './../../../providers/competitions/competition-providers.service';
import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import { AddCompetitionsComponent } from '../add-competition/add-competitions.component';
import { MediaMatcher } from '@angular/cdk/layout';
import { OrderPipe } from 'ngx-order-pipe';

@Component({
  selector: 'app-competition-listings',
  templateUrl: './competition-listings.component.html',
  styleUrls: ['./competition-listings.component.scss']
})



export class CompetitionListingsComponent implements OnInit {

  competitionsRef: any;
  loading: boolean = false;
  competitionData: any;
  competitionsTypesData: any[];
  displayedColumns = ['shortName', 'description', 'type', 'numberOfClubs', 'actions'];
  type: string = 'competition';
  
  mobileQuery: MediaQueryList;
  private _mobileQueryListener: () => void;
  

  @ViewChild(MatSort) sort: MatSort;
  dataSource = new MatTableDataSource(this.competitionData);
  
  constructor(
    public dialog: MatDialog,
    private afs: AngularFirestore,
    private af: AngularFireDatabase,
    public competitionServices: CompetitionProvidersService,
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    private orderPipe: OrderPipe
  ) {
    
    this.mobileQuery = media.matchMedia('(max-width: 768px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);


    this.loading = true;
    this.competitionsRef = afs.collection('competitions');
    competitionServices.getCompetitions().valueChanges().subscribe((data) => {
      this.competitionData = orderPipe.transform(data, 'shortName');
      this.dataSource = new MatTableDataSource(this.competitionData);
      this.loading = false;
    })
  }

  ngOnInit() {
    window.scrollTo(0, 0);
    this.getCompetitionsTypes();
  }
  
  ngAfterViewInit() {
    this.dataSource.sort = this.sort;
  }

  getCompetitionsTypes() {
    this.loading = true;
    this.competitionServices.getCompetitionTypes().valueChanges().subscribe((data) => {
      this.competitionsTypesData = data;
      this.loading = false;
    })
  }

  getCompetitionType(id) {
    if(id && this.competitionsTypesData) {
      let compType = this.competitionsTypesData.find(competitionType => competitionType.id === id);
      return compType;
    }
  }

  openAddCompetition(mode) {
    this.dialog.open(AddCompetitionsComponent, {
      width: '500px',
      data: { id: '', shortName: '', description: '', type: '', numberOfClubs: 0 }
    });
  }
  
  editCompetition(item) {
    this.dialog.open(AddCompetitionsComponent, {
      width: '500px',
      data: item
    });
  }

  deleteCompetition(item) {
    this.competitionServices.deleteCompetition(this.type, item);
  }

  manageGroups(item) {
    this.dialog.open(ManageGroupsComponent, {
      width: '500px',
      data: item
    });
  }
}


