import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFirestore } from 'angularfire2/firestore';
import { HttpModule } from '@angular/http';
import { environment } from './../environments/environment';
import { CdkTableModule } from '@angular/cdk/table';
import { OrderModule } from 'ngx-order-pipe'; // <- import OrderModule
import {
  MatAutocompleteModule, MatButtonModule,
  MatButtonToggleModule, MatCardModule, MatCheckboxModule, MatChipsModule, MatDatepickerModule,
  MatDialogModule, MatDividerModule, MatExpansionModule, MatFormFieldModule, MatGridListModule,
  MatIconModule, MatInputModule, MatListModule, MatMenuModule, MatPaginatorModule,
  MatProgressBarModule, MatProgressSpinnerModule, MatRadioModule, MatRippleModule, MatSelectModule,
  MatSidenavModule, MatSliderModule, MatSlideToggleModule, MatSnackBarModule, MatSortModule,
  MatStepperModule, MatTableModule, MatTabsModule, MatToolbarModule, MatTooltipModule, MatNativeDateModule
} from '@angular/material';

import { AppComponent } from './app.component';
import { SideMenuComponent } from './components/side-menu/side-menu.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './pages/auth/login/login.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { AuthProvidersService } from './providers/auth-providers.service';
import { CommonModule } from '@angular/common';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { CompetitionListingsComponent } from './pages/competitions/competition-listings/competition-listings.component';
import { ClubsListingComponent } from './pages/clubs/clubs-listing/clubs-listing.component';
import { CompetitionProvidersService } from './providers/competitions/competition-providers.service';
import { ConfirmDialogComponent } from './components/confirm-dialog/confirm-dialog.component';
import { ConfirmDialogService } from './providers/dialogs/confirm-dialog.service';
import { AddCompetitionsComponent } from './pages/competitions/add-competition/add-competitions.component';
import { AddClubComponent } from './pages/clubs/add-club/add-club.component';
import { ClubsProvidersService } from './providers/clubs/clubs-providers.service';
import { StandingsComponent } from './pages/matches/standings/standings.component';
import { NewsListingComponent } from './pages/news/news-listing/news-listing.component';
import { AddFixtureComponent } from './pages/matches/fixtures/add-fixture/add-fixture.component';
import { FixturesComponent } from './pages/matches/fixtures/fixtures-listing/fixtures.component';
import { FixturesProvidersService } from './providers/fixtures/fixtures-providers.service';
import { UpdateFixtureComponent } from './pages/matches/fixtures/update-fixture/update-fixture.component';
import { CampaignsComponent } from './pages/campaigns/campaigns.component';
import { AddCampaignComponent } from './pages/campaigns/add-campaign/add-campaign.component';
import { CampaignsService } from './providers/campains/campaigns.service';
import { ManageGroupsComponent } from './pages/competitions/manage-groups/manage-groups.component';
import { CompetitionComponent } from './pages/competitions/competition/competition.component';
import { LogTableComponent } from './components/reusable/log-table/log-table.component';
import { GroupTablesComponent } from './components/reusable/group-tables/group-tables.component';
import { FixturesListingComponent } from './components/reusable/fixtures-listing/fixtures-listing.component';

const appRoutes: Routes = [
  { path: '', component: DashboardComponent ,
    children: [
      { path: '', component: FixturesComponent, data : {pageLevel: 'one'}},
      { path: 'competitions', component: CompetitionListingsComponent, data : {pageLevel: 'one'}},
      { path: 'competitions/:id', component: CompetitionComponent, data : {pageLevel: 'two'}},
      { path: 'clubs', component: ClubsListingComponent, data : {pageLevel: 'one'}},
      { path: 'fixtures', component: FixturesComponent, data : {pageLevel: 'one'}},
      { path: 'campaigns', component: CampaignsComponent, data : {pageLevel: 'one'}},
      { path: 'standings', component: StandingsComponent, data : {pageLevel: 'one'}}
    ]
  },
  { path: 'login', component: LoginComponent },
  { path: 'dashboard', component: DashboardComponent},
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    DashboardComponent,
    PageNotFoundComponent,
    NavbarComponent,
    CompetitionListingsComponent,
    SideMenuComponent,
    FixturesComponent,
    ClubsListingComponent,
    AddCompetitionsComponent,
    ConfirmDialogComponent,
    AddClubComponent,
    StandingsComponent,
    NewsListingComponent,
    AddFixtureComponent,
    UpdateFixtureComponent,
    CampaignsComponent,
    AddCampaignComponent,
    ManageGroupsComponent,
    CompetitionComponent,
    LogTableComponent,
    GroupTablesComponent,
    FixturesListingComponent
  ],
  imports: [
    // Imports
    BrowserModule,
    BrowserAnimationsModule,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: false }
    ),
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    HttpModule,
    CdkTableModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatCardModule,
    MatCheckboxModule,
    MatChipsModule,
    MatDatepickerModule,
    MatDialogModule,
    MatDividerModule,
    MatExpansionModule,
    MatFormFieldModule,
    MatGridListModule,
    MatIconModule,
    MatInputModule,
    MatListModule,
    MatMenuModule,
    MatPaginatorModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatRippleModule,
    MatSelectModule,
    MatSidenavModule,
    MatSlideToggleModule,
    MatSliderModule,
    MatSnackBarModule,
    MatSortModule,
    MatStepperModule,
    MatTableModule,
    MatTabsModule,
    MatToolbarModule,
    MatTooltipModule,
    MatNativeDateModule,
    OrderModule
  ],
  // Exports
  exports: [
    CdkTableModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatCardModule,
    MatCheckboxModule,
    MatChipsModule,
    MatDatepickerModule,
    MatDialogModule,
    MatDividerModule,
    MatExpansionModule,
    MatFormFieldModule,
    MatGridListModule,
    MatIconModule,
    MatInputModule,
    MatListModule,
    MatMenuModule,
    MatPaginatorModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatRippleModule,
    MatSelectModule,
    MatSidenavModule,
    MatSlideToggleModule,
    MatSliderModule,
    MatSnackBarModule,
    MatSortModule,
    MatStepperModule,
    MatTableModule,
    MatTabsModule,
    MatToolbarModule,
    MatTooltipModule
],
// Providers
providers: [
  AuthProvidersService,
  AngularFirestore,
  CompetitionProvidersService,
  ConfirmDialogService,
  ClubsProvidersService,
  FixturesProvidersService,
  CampaignsService
],
// Entry components
entryComponents: [
  CompetitionListingsComponent,
  AddCompetitionsComponent,
  ConfirmDialogComponent,
  AddClubComponent,
  AddFixtureComponent,
  UpdateFixtureComponent,
  AddCampaignComponent,
  ManageGroupsComponent
],
// Bootstrap
bootstrap: [AppComponent]
})
export class AppModule { }



