import { Pipe } from '@angular/core';
import { CompetitionProvidersService } from './../../../providers/competitions/competition-providers.service';
import { AddClubComponent } from './../../clubs/add-club/add-club.component';
import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { AngularFirestore } from 'angularfire2/firestore';
import { MatDialog, MatSort, MatTableDataSource } from '@angular/material';
import { ClubsProvidersService } from '../../../providers/clubs/clubs-providers.service';
import { MediaMatcher } from '@angular/cdk/layout';
import { OrderPipe } from 'ngx-order-pipe';

@Component({
  selector: 'app-clubs-listing',
  templateUrl: './clubs-listing.component.html',
  styleUrls: ['./clubs-listing.component.scss']
})
export class ClubsListingComponent implements OnInit {
  private _mobileQueryListener: () => void;
  competitionsTypesData: any[];
  competitionsData: any[];
  clubsData: any[];
  loading: boolean;
  mobileQuery: MediaQueryList;
  displayedColumns = ['name', 'homeGround', 'contacts', 'actions'];
  type = 'club';

  // @ViewChild(MatSort) sort: MatSort;
  dataSource = new MatTableDataSource(this.clubsData);
  constructor(
    public dialog: MatDialog,
    private afs: AngularFirestore,
    private af: AngularFireDatabase,
    public competitionServices: CompetitionProvidersService,
    public clubsService: ClubsProvidersService,
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    orderPipe: OrderPipe
  ) {

    this.mobileQuery = media.matchMedia('(max-width: 767px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);

    this.loading = true;
    clubsService.getClubs().valueChanges().subscribe((data) => {
      this.clubsData = orderPipe.transform(data, 'name');
      this.dataSource = new MatTableDataSource(this.clubsData);
      this.loading = false;
    })

    this.getCompetitions();
    this.getCompetitionsTypes();
  }

  ngOnInit() {
    window.scrollTo(0, 0);
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  getCompetitions() {
    this.loading = true;
    this.competitionServices.getCompetitions().valueChanges().subscribe((data) => {
      this.competitionsData = data;
      this.loading = false;
    })
  }

  getCompetitionsTypes() {
    this.loading = true;
    this.competitionServices.getCompetitionTypes().valueChanges().subscribe((data) => {
      this.competitionsTypesData = data;
      this.loading = false;
    })
  }

  // FILTERS
  getCompetition(id) {
    if(id && this.competitionsData) {
      let comp = this.competitionsData.find(competition => competition.id === id);
      return comp;
    }
  }

  getCompetitionTypeName(id) {
    if(id && this.competitionsTypesData) {
      let compType = this.competitionsTypesData.find(competitionType => competitionType.id === id);
      return compType.name;
    }
  }

  onCompetitionChange(event:Event):void{
    if(event) {
      let result = this.clubsData.filter(item => item.competitions.indexOf(event) > -1);
      this.dataSource = new MatTableDataSource(result);
    }
    else {
      this.dataSource = new MatTableDataSource(this.clubsData);
    }
  }

  openAddClub() {
    this.dialog.open(AddClubComponent, {
      width: '500px',
      data: null
    });
  }
  
  editClub(item) {
    this.dialog.open(AddClubComponent, {
      width: '500px',
      data: item
    });
  }

  deleteClub(item) {
    console.log(item);
    this.clubsService.deleteClub(this.type, item);
  }
}


