import { CampaignsService } from './../../providers/campains/campaigns.service';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { MediaMatcher } from '@angular/cdk/layout';
import { MatDialog, MatTableDataSource } from '@angular/material';
import { AngularFirestore } from 'angularfire2/firestore';
import { AngularFireDatabase } from 'angularfire2/database';
import { AddCampaignComponent } from './add-campaign/add-campaign.component';
import { OrderPipe } from 'ngx-order-pipe';

@Component({
  selector: 'app-campaigns',
  templateUrl: './campaigns.component.html',
  styleUrls: ['./campaigns.component.scss']
})
export class CampaignsComponent implements OnInit {
  private _mobileQueryListener: () => void;
  campaignsData: any;
  loading: boolean;
  mobileQuery: MediaQueryList;
  displayedColumns = ['campaign', 'startYear', 'endYear', 'actions'];
  type = 'campaign';

  dataSource = new MatTableDataSource(this.campaignsData);
  constructor(
    
    public dialog: MatDialog,
    private afs: AngularFirestore,
    private af: AngularFireDatabase,
    changeDetectorRef: ChangeDetectorRef,
    public campaignsService: CampaignsService,
    media: MediaMatcher,
    private orderPipe: OrderPipe
  ) {
    this.mobileQuery = media.matchMedia('(max-width: 767px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);

    this.loading = true;
    campaignsService.getCampaigns().valueChanges().subscribe((data) => {
      this.campaignsData = orderPipe.transform(data, 'endYear', true);
      this.dataSource = new MatTableDataSource(this.campaignsData);
      this.loading = false;
    })
  }

  ngOnInit() {
    window.scrollTo(0, 0);
  }

  toggleBodyScrolling() {
    
  }

  openAddCampaign() {
    this.dialog.open(AddCampaignComponent, {
      width: '500px',
      data: null
    });
  }
  
  editCampaing(item) {
    this.dialog.open(AddCampaignComponent, {
      width: '500px',
      data: item
    });
  }

  deleteCompaign(item) {
    console.log(item);
    this.campaignsService.deleteCampaign(this.type, item);
  }

}



