
import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ClubsProvidersService } from '../../../providers/clubs/clubs-providers.service';
import { CompetitionProvidersService } from './../../../providers/competitions/competition-providers.service';

@Component({
  selector: 'app-add-club',
  templateUrl: './add-club.component.html',
  styleUrls: ['./add-club.component.scss']
})
export class AddClubComponent implements OnInit {

  loading: boolean;
  clubData: any;
  competitionsData: any[];
  competitions: any[];
  competitionTypeData: Array<String> = [];
  form: FormGroup;
  clubID: any;
  competitionInput = new FormControl();

  constructor(
    public dialogRef: MatDialogRef<AddClubComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public competitionServices: CompetitionProvidersService,
    public clubService: ClubsProvidersService,
    private formBuilder: FormBuilder
  ) {
    this.loading = true;
    this.competitionServices.getCompetitions().valueChanges().subscribe((res) => {
      this.loading = false;
      this.competitionsData = res;
    });

    
    this.competitionInput = new FormControl( this.data ? this.data.competitions : [], Validators.required );
    this.form = this.formBuilder.group({
      name: [this.data ? this.data.name : null, Validators.required],
      homeGround: [this.data ? this.data.homeGround : null],
      emailAddress: [this.data ? this.data.emailAddress : null],
      physicalAddress: [this.data ? this.data.physicalAddress : null],
      contactNumber: [this.data ? this.data.contactNumber : null],
      website: [this.data ? this.data.website : null],
      facebookUrl: [this.data ? this.data.facebookUrl : null],
      twitterUrl: [this.data ? this.data.twitterUrl : null],
      instagramUrl: [this.data ? this.data.instagramUrl : null],
      youtubeUrl: [this.data ? this.data.youtubeUrl : null]
    });
  }

  ngOnInit() {
  }

  onCancel(): void {
    this.dialogRef.close();
  }

  // Returns name of competition using id
  getCompetitonName(id) {
    if(id && this.competitionsData) {
      let competitionName = this.competitionsData.find(competition => competition.id === id);
      return competitionName.description;
    }
  }

  addCompetition() {
    
    this.clubData = this.form.value;
    this.clubData.id = this.data ? this.data.id : null;
    this.clubData.competitions = this.competitionInput.value;
    this.clubData.logoUrl = '';
    if(this.clubData.id) { this.clubService.updateClub(this.clubData); }
    else { this.clubService.addClub(this.clubData); }
    this.dialogRef.close();
  }
}



