import { CampaignsService } from './../../../../providers/campains/campaigns.service';
import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { CompetitionProvidersService } from '../../../../providers/competitions/competition-providers.service';
import { ClubsProvidersService } from '../../../../providers/clubs/clubs-providers.service';
import { FixturesProvidersService } from '../../../../providers/fixtures/fixtures-providers.service';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-add-fixture',
  templateUrl: './add-fixture.component.html',
  styleUrls: ['./add-fixture.component.scss']
})
export class AddFixtureComponent implements OnInit {

  clubDetails: any;
  allClubsData: any[];
  campaignsData: any[];
  competitionsData: any[];
  fixtureData: any;
  clubsData: any[];
  loading: boolean;
  form: FormGroup;

  constructor(
    public dialogRef: MatDialogRef<AddFixtureComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public competitionServices: CompetitionProvidersService,
    public clubsService: ClubsProvidersService,
    public campaignsService: CampaignsService,
    private formBuilder: FormBuilder,
    public fixturesService: FixturesProvidersService,
  ) {
    this.form = this.formBuilder.group({
      competition: [this.data ? this.data.competition : null, Validators.required],
      campaign: [this.data ? this.data.campaign : this.campaignsData[0].id, Validators.required],
      homeClub: [this.data ? this.data.homeClub : null, Validators.required],
      awayClub: [this.data ? this.data.awayClub : null, Validators.required],
      date: [this.data ? this.data.date : null],
      vanue: [this.data ? this.data.vanue : null],
      kickOffTime: [this.data ? this.data.kickOffTime : null],
      hasTwoLegs: [this.data ? this.data.hasTwoLegs : null],
    });


  }

  ngOnInit() {
    this.getClubs();
    this.getCompetitions();
    this.getCampaigns();
  }

  onCancel(): void {
    this.dialogRef.close();
  }

  onCompetitionChange(event:Event) {
    if(event && this.clubsData) {
      this.clubsData = this.allClubsData.filter(item => item.competitions.indexOf(event) > -1);
    }
  }

  onHomeClubChange(event:Event) {
    this.clubDetails = this.allClubsData.find(item => item.id === event);
    this.form.patchValue({
      vanue: this.clubDetails.homeGround ? this.clubDetails.homeGround : '',
    });
  }

  getClubs() {
    this.loading = true;
    this.clubsService.getClubs().valueChanges().subscribe((data) => {
      this.allClubsData = data;
      this.clubsData = this.allClubsData;
      this.onCompetitionChange(this.data.competition)
      this.loading = false;
    })
  }

  getCampaigns() {
    this.loading = true;
    this.campaignsService.getCampaigns().valueChanges().subscribe((data) => {
      this.campaignsData = data;
      this.form.patchValue({campaign: this.campaignsData[0].id });
      this.loading = false;
    })
  }

  getCompetitions() {
    this.loading = true;
    this.competitionServices.getCompetitions().valueChanges().subscribe((res) => {
      this.loading = false;
      this.competitionsData = res;
    });
  }

  addFixture() {
    this.fixtureData = this.form.value;
    this.fixtureData.id = this.data.id ? this.data.id : null;
    this.fixtureData.status = this.data.status ? this.data.status : 'UPCOMING';
    if(this.fixtureData.id) { this.fixturesService.updateFixture(this.fixtureData); }
    else { this.fixturesService.addFixture(this.fixtureData); }
    this.dialogRef.close();
  }
}



