import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { CompetitionProvidersService } from '../../../../providers/competitions/competition-providers.service';
import { ClubsProvidersService } from '../../../../providers/clubs/clubs-providers.service';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { FixturesProvidersService } from '../../../../providers/fixtures/fixtures-providers.service';
import { CampaignsService } from '../../../../providers/campains/campaigns.service';

@Component({
  selector: 'app-update-fixture',
  templateUrl: './update-fixture.component.html',
  styleUrls: ['./update-fixture.component.scss']
})
export class UpdateFixtureComponent implements OnInit {
  
  campaignsData: any[];
  competitionsData: any[];
  fixtureData: any;
  clubsData: any[];
  loading: boolean;
  form: FormGroup;

  constructor(
    public dialogRef: MatDialogRef<UpdateFixtureComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public competitionServices: CompetitionProvidersService,
    public clubsService: ClubsProvidersService,
    private formBuilder: FormBuilder,
    public fixturesService: FixturesProvidersService,
    public campaignsService: CampaignsService
  ) {
    
    this.form = this.formBuilder.group({
      competition: [this.data ? this.data.competition : null, Validators.required],
      homeClub: [this.data ? this.data.homeClub : null, Validators.required],
      campaign: [this.data ? this.data.campaign : null, Validators.required],
      awayClub: [this.data ? this.data.awayClub : null, Validators.required],
      homeScore: [this.data ? this.data.homeScore : null, Validators.required],
      awayScore: [this.data ? this.data.awayScore : null, Validators.required],
      isDecidedOnPenalties: [this.data ? this.data.isDecidedOnPenalties : false],
      homePenaltyScore: [this.data ? this.data.homePenaltyScore : null, Validators.nullValidator],
      awayPenaltyScore: [this.data ? this.data.awayPenaltyScore : null, Validators.nullValidator]
    });

    this.form.controls.competition.disable();
    this.form.controls.homeClub.disable();
    this.form.controls.awayClub.disable();
    this.form.controls.campaign.disable();
  }

  ngOnInit() {
    this.getClubs();
    this.getCompetitions();
    this.getCampaigns();
  }

  onCancel(): void {
    this.dialogRef.close();
  }

  getClubs() {
    this.loading = true;
    this.clubsService.getClubs().valueChanges().subscribe((data) => {
      this.clubsData = data;
      this.loading = false;
    })
  }

  getCampaigns() {
    this.loading = true;
    this.campaignsService.getCampaigns().valueChanges().subscribe((data) => {
      this.campaignsData = data;
      this.loading = false;
    })
  }

  getCompetitions() {
    this.loading = true;
    this.competitionServices.getCompetitions().valueChanges().subscribe((res) => {
      this.loading = false;
      this.competitionsData = res;
    });
  }

  addFixture() {
    this.fixtureData = this.form.value;
    this.fixtureData.id = this.data.id ? this.data.id : null;
    this.fixtureData.status = 'PLAYED';
    if(this.fixtureData.id) {
      this.fixturesService.updateFixture(this.fixtureData);
      this.dialogRef.close();
    }
  }
}



