import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { ConfirmDialogService } from '../dialogs/confirm-dialog.service';
import { MatSnackBar } from '@angular/material';

@Injectable()
export class ClubsProvidersService {
  clubsDB: AngularFirestoreCollection<any> = this.afs.collection('clubs');

  constructor(
    private afs: AngularFirestore,
    public confirmDialog: ConfirmDialogService,
    public snackBar: MatSnackBar
  ) {}


  getClubTypes() {
    return this.afs.collection('compTypes');
  }

  getClubs() {
    return this.clubsDB;
  }

  getClubDetails(id) {
    let clubDetails = this.afs.collection('clubs', ref => ref.where('id', '==', id));
    return clubDetails;
  }

  getClubByCompetition(id) {
    let itemsCollection = this.afs.collection('clubs');
    let items = itemsCollection;
    console.log(items);
  }
  

  addClub(data) {
    return this.clubsDB.add(data).then(res => {
      res.update({ id: res.id }).then(response => {
        this.snackBar.open('Club added', 'CLOSE', {
          duration: 2000,
        });
      });
    })
  }

  updateClub(data) {
    return this.clubsDB.doc(data.id).update(data).then(response => {
      this.snackBar.open('Club updated', 'CLOSE', {
        duration: 2000,
      });
    });
  }


  deleteClub(type, data) {
    this.confirmDialog.openConfirmDialog(type, data).subscribe(res => {
      if(res) {
        return this.clubsDB.doc(data.id).delete().then(a => {
          this.snackBar.open(type + ' deleted', 'CLOSE', {
            duration: 2000,
          });
        });
      }
    })
  }
}



