import { Injectable } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore } from 'angularfire2/firestore';
import { ConfirmDialogService } from '../dialogs/confirm-dialog.service';
import { MatSnackBar } from '@angular/material';

@Injectable()
export class FixturesProvidersService {

  fixturesDB: AngularFirestoreCollection<any> = this.afs.collection('fixtures');
  constructor(
    private afs: AngularFirestore,
    public confirmDialog: ConfirmDialogService,
    public snackBar: MatSnackBar
  ) { }

  getAllFixtures() {
    return this.fixturesDB;
  }

  getFixturesByCompetitionID(id) {
    let filteredByCompetition = this.afs.collection('fixtures', ref => ref.where('competition', '==', id)).valueChanges();
    return filteredByCompetition;
  }

  getFixturesByClubID(id) {
    let homeFixtures = this.afs.collection('fixtures', ref => ref.where('homeClub', '==', id)).valueChanges();
    let awayFixtures = this.afs.collection('fixtures', ref => ref.where('awayClub', '==', id)).valueChanges();
    return homeFixtures;
  }


  addFixture(data) {
    return this.fixturesDB.add(data).then(res => {
      res.update({ id: res.id }).then(response => {
        this.snackBar.open('Fixture added', 'CLOSE', {
          duration: 2000,
        });
      });
    })
  }

  updateFixture(data) {
    return this.fixturesDB.doc(data.id).update(data).then(response => {
      this.snackBar.open('Fixture updated', 'CLOSE', {
        duration: 2000,
      });
    });
  }

  deleteFixture(type, data) {
    this.confirmDialog.openConfirmDialog(type, data).subscribe(res => {
      if(res) {
        return this.fixturesDB.doc(data.id).delete().then(a => {
          this.snackBar.open(type + ' deleted', 'CLOSE', {
            duration: 2000,
          });
        });
      }
    })
  }

}



