import { Injectable } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Rx';
import { CompetitionsModel } from '../../models/competitions-model';
import { ConfirmDialogService } from '../dialogs/confirm-dialog.service';
import { MAT_SNACK_BAR_DATA, MatSnackBar } from '@angular/material';

@Injectable()
export class CompetitionProvidersService {
  snackBarRef: any;
  competitionsDB: AngularFirestoreCollection<any> = this.afs.collection('competitions');
  competitionTypesDB: AngularFirestoreCollection<any> = this.afs.collection('compTypes');

  
  constructor(
    private afs: AngularFirestore,
    public confirmDialog: ConfirmDialogService,
    public snackBar: MatSnackBar
  ) {}

  getCompetitionTypes() {
    return this.afs.collection('compTypes');
  }
  getCompetitionTypeByID(id) {
    return this.competitionTypesDB.doc(id).ref.get().then(function(doc) {
      return doc.data();
    }).catch(function(error) {
        console.log("Error getting document:", error);
    });
  }

  getCompetitions() {
    return this.competitionsDB;
  }

  addCompetition(data) {
    return this.competitionsDB.add(data).then(res => {
      res.update({ id: res.id }).then(response => {
        this.snackBar.open('Competition added', 'CLOSE', {
          duration: 2000,
        });
      });
    })
  }

  
  getCompetitionByID(id) {
    return this.competitionsDB.doc(id).ref.get().then(function(doc) {
      return doc.data();
    }).catch(function(error) {
        console.log("Error getting document:", error);
    });
  }
  
  updateCompetition(data) {
    return this.competitionsDB.doc(data.id).update(data).then(response => {
      this.snackBar.open('Competition updated', 'CLOSE', {
        duration: 2000,
      });
    });
  }

  deleteCompetition(type, data) {
    this.confirmDialog.openConfirmDialog(type, data).subscribe(res => {
      if(res) {
        return this.competitionsDB.doc(data.id).delete().then(a => {
          this.snackBar.open(type + ' deleted', 'CLOSE', {
            duration: 2000,
          });
        });
      }
    })
  }
}



