import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase } from 'angularfire2/database';
import { AngularFirestore, AngularFirestoreDocument } from 'angularfire2/firestore';
import * as firebase from 'firebase/app';

import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/switchMap'

interface User {
  email: string;
  photoURL?: string;
  displayName?: string;
}


@Injectable()
export class AuthProvidersService {
  user: Observable<User>;

  constructor(public afAuth: AngularFireAuth, public afd: AngularFireDatabase, private afs: AngularFirestore) {}

  loginUser(newEmail: string, newPassword: string): Promise<any> {
    return this.afAuth.auth.signInWithEmailAndPassword(newEmail, newPassword);
  }

  resetPassword(email: string): Promise<any> {
    return this.afAuth.auth.sendPasswordResetEmail(email);
  }

  logoutUser(): Promise<any> {
    return this.afAuth.auth.signOut();
  }

  signupUser(newEmail: string, newPassword: string, name: string): Promise<any> {

    return this.afAuth.auth.createUserAndRetrieveDataWithEmailAndPassword(newEmail, newPassword).then(() =>
      this.afAuth.auth.currentUser.updateProfile({
        displayName: name,
        photoURL:"null"
      })
    );
  }

  currentUser(): any {
    return this.afAuth.authState
  }

}



