import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';
import { Router } from "@angular/router";
import { AuthProvidersService } from '../../../providers/auth-providers.service';
import { UIkit } from 'uikit';
import { MatSnackBar } from '@angular/material';
declare var UIkit: any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  currentUser: any;

  email: string;
  password: string;

  loading: boolean = false;
  
  constructor(
    public authData: AuthProvidersService,
    private router: Router,
    public snackBar: MatSnackBar
  ) {
    authData.currentUser().subscribe((data) => {
      this.currentUser = data;
      if(this.currentUser) {
        this.router.navigate(['/']);
      }
    });
  }

  ngOnInit() {
    window.scrollTo(0, 0);
  }

  loginUser() {
    this.loading = true;
    this.authData.loginUser(this.email, this.password)
      .then( authData => {
        this.loading = false;
        this.router.navigate(['/']);

        this.snackBar.open('Welcome', 'CLOSE', {
          duration: 2000,
        });
      }, (error) => {
        this.snackBar.open(error.message, 'CLOSE', {
          duration: 2000,
        });
        this.loading = false;
      });
    }

}


