import { Component, OnInit, Inject } from '@angular/core';
import { CampaignsService } from '../../../providers/campains/campaigns.service';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { CompetitionProvidersService } from '../../../providers/competitions/competition-providers.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';

@Component({
  selector: 'app-add-campaign',
  templateUrl: './add-campaign.component.html',
  styleUrls: ['./add-campaign.component.scss']
})
export class AddCampaignComponent implements OnInit {
  clubData: any;
  form: FormGroup;
  currentYear = new Date().getFullYear()
  years = [];
  yearsData: any;

  constructor(
    public campaignsService: CampaignsService,
    public dialogRef: MatDialogRef<AddCampaignComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public competitionServices: CompetitionProvidersService,
    private formBuilder: FormBuilder
  ) {
    this.form = this.formBuilder.group({
      campaign: [this.data ? this.data.campaign : null, Validators.required],
      startYear: [this.data ? this.data.startYear : null],
      endYear: [this.data ? this.data.endYear : null],
    });
  }

  ngOnInit() {
    this.generateListOfYears(2017);
    this.yearsData = this.generateListOfYears(2017).filter((v, i, a) => a.indexOf(v) === i);
  }

  onCancel(): void {
    this.dialogRef.close();
  }

  onYearChange(start, end) {
    let startYearStr = start ? start.toString() : this.data.startYear.toString();
    let endYearStr = end ? end.toString() : this.data.endYear.toString();
    if(startYearStr && endYearStr) {
      this.form = this.formBuilder.group({
        campaign: startYearStr +'/'+ endYearStr.substr(endYearStr.length - 2),
        startYear: [startYearStr ? start : null],
        endYear: [endYearStr ? end : null],
      });
    }
  }

  generateListOfYears(startYear) {
    startYear = startYear;

    while ( startYear <= this.currentYear + 5 ) {
      this.years.push(startYear++);
    } 

    return this.years;
}

  addCampaign() {
    this.clubData = this.form.value;
    this.clubData.id = this.data ? this.data.id : null;
    if(this.clubData.id) { this.campaignsService.updateCampaign(this.clubData); }
    else { this.campaignsService.addCampaign(this.clubData); }
    this.dialogRef.close();
  }
}



