import { Injectable } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore } from 'angularfire2/firestore';
import { ConfirmDialogService } from '../dialogs/confirm-dialog.service';
import { MatSnackBar } from '@angular/material';

@Injectable()
export class CampaignsService {

  snackBarRef: any;
  campaigndDB: AngularFirestoreCollection<any> = this.afs.collection('campaigns');

  
  constructor(
    private afs: AngularFirestore,
    public confirmDialog: ConfirmDialogService,
    public snackBar: MatSnackBar
  ) {}

  getCampaigns() {
    return this.campaigndDB;
  }

  addCampaign(data) {
    return this.campaigndDB.add(data).then(res => {
      res.update({ id: res.id }).then(response => {
        this.snackBar.open('Campaign added', 'CLOSE', {
          duration: 2000,
        });
      });
    })
  }

  
  getCampaignByID(id) {
    return this.campaigndDB.doc(id).ref.get().then(function(doc) {
      return doc.data();
    }).catch(function(error) {
        console.log("Error getting document:", error);
    });
  }
  
  updateCampaign(data) {
    return this.campaigndDB.doc(data.id).update(data).then(response => {
      this.snackBar.open('Campaigns updated', 'CLOSE', {
        duration: 2000,
      });
    });
  }

  deleteCampaign(type, data) {
    this.confirmDialog.openConfirmDialog(type, data).subscribe(res => {
      if(res) {
        return this.campaigndDB.doc(data.id).delete().then(a => {
          this.snackBar.open(type + ' deleted', 'CLOSE', {
            duration: 2000,
          });
        });
      }
    })
  }
}



